import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {HttpClient, HttpEventType} from '@angular/common/http';
import {AuthService} from '../../../services/auth.service';
import {UploadService} from '../../../services/upload.service';
import {Constants} from '../../../app.constants';
import {AppData} from '../../../data';
import {IPicture} from '../../../interfaces/interfaces';
import {Router} from '@angular/router';

@Component({
    selector: 'app-upload',
    templateUrl: './upload.component.html',
    styleUrls: ['./upload.component.scss']
})
export class UploadComponent implements OnInit {

    public progress: number;
    public message: string;
    @Output() public uploadFinished = new EventEmitter();
    @Output() public updateUploadStatus = new EventEmitter();
    @Input() public id = '';
    @Input() public fileType = '';
    @Input() public showLabel = true;
    @Input() public inputName = 'picture';
    @Input() public serverUrlRoute = 'pictures';
    public uploading = {
        status: false,
        error: ''
    };

    constructor(private authService: AuthService,
                private uploadService: UploadService,
                private http: HttpClient, private router: Router) {
        // console.log('here in upload component');
        this.uploadService.trigger.subscribe(command => {
            if (this.id === command) {
                const button = document.getElementById('upload-file-input-' + this.id);
                if (button) {
                    button.nodeValue = '';
                    button.click();
                }
            }
        });
    }

    ngOnInit() {
    }

    public uploadFile(event) {
        const oldEvent = event;
        const files = event.target.files;
        if (files.length === 0) {
            return;
        }

        const fileToUpload = <File>files[0];
        if (this.fileType) {
            const extension = AppData.FILETYPES[this.fileType];
            if (extension && fileToUpload.type.indexOf(extension.ext) < 0) {
                oldEvent.target.value = '';
                Constants.error('Invalid file', extension.desc + ' with extension ' + extension.xamp + ' is required');
                return;
            }
        }
        const formData = new FormData();
        formData.append(this.inputName, fileToUpload, fileToUpload.name);

        this.message = '';

        this.uploading.status = true;
        this.updateUploadStatus.emit({type: 'progress', value: 0});
        this.http.post(Constants.url + this.serverUrlRoute, formData,
            {reportProgress: true, observe: 'events', headers: Constants.getUploadHeader()}).subscribe((evt: any) => {
            if (evt.type === HttpEventType.UploadProgress) {
                this.progress = Math.round(100 * evt.loaded / evt.total);
                this.updateUploadStatus.emit({type: 'progress', value: this.progress});
            } else if (evt.type === HttpEventType.Response) {
                const upload = <IPicture>evt.body.picture;
                this.uploading.status = false;
                Constants.succeeded(upload.seo_filename + ' uploaded successfully', 'Upload successful');
                this.uploadFinished.emit(upload);

                oldEvent.target.value = '';
            }
        }, (error) => {
            this.uploading.status = false;
            Constants.handleError(error, this.router, true);
            this.message = '';
            oldEvent.target.value = '';
            this.updateUploadStatus.emit({type: 'error', value: this.progress});
        });
    }
}
