import {Component, Input, OnInit, ViewEncapsulation} from '@angular/core';
import {ChatMessage} from '../../../interfaces/interfaces';
import {ChatService} from '../../../services/chat.service';
import {Constants} from '../../../app.constants';

@Component({
    selector: 'app-light-box',
    templateUrl: './light-box.component.html',
    styleUrls: ['./light-box.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class LightBoxComponent implements OnInit {

    show = false;
    slideIndex = 1;
    @Input() slides: ChatMessage[] = [];

    placeholders = {
        audio: Constants.hostUrl + '/assets/img/media/audio.png',
        unsupported: Constants.hostUrl + '/assets/img/media/unsupported.png',
        video: Constants.hostUrl + '/assets/img/media/video.jpg'
    };

    constructor(public chatService: ChatService) {
    }

    ngOnInit() {
        this.show = false;
    }

    open(index = this.slideIndex) {
        this.show = true;
        this.toSlide(index);
    };

    close() {
        this.show = false;
    };

    changeSlide(n) {
        this.toSlide((this.slideIndex + n));
    };

    toSlide(n) {
        if (n >= this.slides.length) {
            n = 0;
        }

        if (n < 0) {
            n = this.slides.length - 1;
        }
        this.slideIndex = n;
    };

    getExtension(path) {
        path = path.toString().toLowerCase();
        const re = /(?:\.([^.]+))?$/;
        let ext = re.exec(path)[1] || '';
        if (ext === 'mp3' || ext === 'mpga') {
            ext = 'mpeg';
        }

        console.log(ext);

        return ext;
    }

}
